import {mapState} from 'vuex'
export default {
	data() {
		return {
			rubbishtypenum:'',
			type:''
		}
	},
	computed:{
		...mapState([
			'finalobj'
		])
	},
	onLoad(){
		var type = this.finalobj.type == 0 ? '可回收物' : this.finalobj.type == 1 ? '有害垃圾' : this.finalobj.type == 2 ? '厨余(湿)垃圾' : this.finalobj.type == 3 ? '其他(干)垃圾' : ''
		this.rubbishtypenum = this.finalobj.type
		this.type = type
	},
	methods: {
		
	}
}
